export default {
  keyToClass (v) {
    let className = ''
    /* eslint-disable */
    className =
      v === ''
        ? 'fire'
        : v === ''
          ? 'water'
          : v === ''
            ? 'thunder'
            : v === ''
              ? 'ice'
              : v === ''
                ? 'dragon'
                : v === ''
                  ? 'poison'
                  : v === ''
                    ? 'sleep'
                    : v === ''
                      ? 'paralysis'
                      : v === '' ? 'blast' : v === '' ? 'stun' : null
    /* eslint-enable */
    return className
  },
}



